.us_btn {
  display: inline-block;
  line-height: 50px;
  height: 50px;
  box-sizing: border-box;
  padding: 0 15px;
  background: #da3743;
  border: none;
  color: #fff;
  font-size: 18px;
  font-weight: 300;
  transition: opacity $usTransition;

  &:hover {
    opacity: .8;
  }
}

.us_link {
  color: rgba(1, 87, 155, .8);
}