.header {
  position: relative;
  z-index: 2;
  height: 50px;
  line-height: 50px;

  &.header--index {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, .25);

    .header__link,
    .header__user {
      color: #fff;

      &:hover {
        color: #E1F5FE;
      }
    }
  }

  .header__left {
    float: left;
  }

  .header__right {
    float: right;
  }

  .header__user {
    color: inherit;
    text-decoration: none;

    .user__image {
      display: inline-block;
      vertical-align: middle;
      overflow: hidden;
      margin-right: 10px;
      width: 30px;
      height: 30px;
      border-radius: 50px;
      background: {
        size: cover;
        position: center;
      }
    }
  }

  .header__link {
    color: rgba(90, 90, 90, 1);
    text-decoration: none;
    margin: 0 20px;
    transition: color .3s ease;

    &:hover {
      color: rgba(90, 90, 90, .8);
    }

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  .header__add_boat {
    display: inline-block;
    vertical-align: middle;
    height: 30px;
    line-height: 30px;
    padding: 0 15px;
    margin-left: 20px;
    border-radius: 3px;
    color: #fff;
    background: #0277BD;
    font-size: 14px;
    text-decoration: none;
    transition: background $usTransition;

    &:hover {
      background: #01579B;
    }
  }

  .header__logo {
    display: inline-block;
    vertical-align: middle;
    width: 100px;
    height: 25px;
    margin-right: 25px;
    background: url(../images/logo.png) center / contain no-repeat;
  }
}