.footer {
  background: $blue $pattern;
  padding: 50px 0 90px;
  color: #fff;

  .footer__inner {
    position: relative;
    overflow: hidden;
  }

  .footer__block {
    float: left;
    width: 33.3333%;
    box-sizing: border-box;
    padding-right: 30px;

    .block__title {
      font-weight: 600;
    }

    .footer__links {
      margin: 15px 0 0;
      padding: 0;
      list-style: none;

      .links__item {
        .item__link {
          display: block;
          margin-top: 5px;
          font-size: 14px;
          text-decoration: none;
          color: inherit;

          &:hover {
            color: #ccc;
          }
        }
      }
    }

    .footer__social {
      .social__item {
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        width: 30px;
        height: 30px;
        border-radius: 30px;
        line-height: 30px;
        text-align: center;
        text-decoration: none;
        color: $blue;
        background: #fff;

        .icon {
          display: inline-block;
          vertical-align: middle;
        }

        &:hover {
          background: #ccc;
        }
      }
    }

    .footer__copy {
      font-size: 12px;
      font-weight: 300;
      margin-top: 15px;
    }
  }
}