.index_search {
  position: relative;
  height: 700px;
  overflow: hidden;

  .index_search__background {
    position: relative;
    height: 100%;

    &:before {
      content: "";
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: $pattern;
      opacity: .5;
    }

    .background__video {
      position: relative;
      min-width: 100%;
      min-height: 100%;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
    }
  }

  .index_search__content {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .1);

    .content__promo {
      position: relative;
      height: 600px;
      text-align: center;
      color: #fff;

      .promo__inner {
        position: absolute;
        left: 0;
        right: 0;
        top: 35%;
      }

      .promo__title {
        margin-bottom: 5px;

        h1 {
          margin: 0;
          font-weight: 700;
          font-size: 40px;
          text-shadow: 2px 2px rgba(0, 0, 0, .25);
        }
      }

      .promo__desc {
        font-size: 18px;
        font-weight: 400;
        text-shadow: 1px 1px rgba(0, 0, 0, .5);
      }

      .promo__btn {
        margin-top: 25px;

        .promo__btn__item {
          display: inline-block;
          vertical-align: middle;
          color: #fff;
          height: 40px;
          line-height: 40px;
          padding: 0 30px;
          border-radius: 3px;
          background: rgba(0, 0, 0, .2);
          text-decoration: none;
          font-size: 20px;
          transition: background $usTransition;

          &:hover {
            background: rgba(0, 0, 0, .4);
          }
        }
      }
    }

    .content__search {
      height: 100px;
      line-height: 100px;
      background: rgba(0, 0, 0, .5);
      text-align: center;

      .search__form {
        display: inline-block;
        vertical-align: middle;
        line-height: normal;

        .form__input {
          height: 50px;
          padding: 0 15px;
          border: none;
          outline: none;
          border-top-left-radius: 3px;
          border-bottom-left-radius: 3px;
          width: 240px;
        }

        .form__btn {
          line-height: 50px;
          padding: 0 15px;
          border: none;
          border-top-right-radius: 3px;
          border-bottom-right-radius: 3px;
          background: rgba(1, 87, 155, .8);
          color: #fff;
          transition: background $usTransition;

          &:hover {
            background: rgba(1, 87, 155, 1);
          }
        }
      }
    }
  }
}
