.overlay {
  position: fixed;
  display: none;
  opacity: 0;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  background: rgba(0, 0, 0, .85);
  pointer-events: none;
  transition: all .5s $fastStartEase;

  &.active {
    opacity: 1;
    pointer-events: all;
  }

  .overlay__table {
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
  }

  .overlay__cell {
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: middle;
    padding: 50px;
  }

  .overlay__popup {
    display: none;
    margin: 0 auto;
    transform: translateY(-50%);
    opacity: 0;

    &.active {
      opacity: 1;
      transform: translateY(0);
      transition: all .5s $fastStartEase;
    }

    &.faded {
      opacity: 0;
      transform: translateY(50%);
      transition: all .5s $fastStartEase;
    }

    .popup__title {
      font-size: 24px;
      font-weight: 300;
      margin-bottom: 15px;
    }

    .popup__form {
      .form__social {
        .social__item {
          .item__btn {
            display: inline-block;
            vertical-align: middle;
            text-decoration: none;
            line-height: 40px;
            height: 40px;
            padding: 0 15px;

            .icon {
              position: relative;
              display: inline-block;
              vertical-align: middle;
              margin-left: 5px;
              font-size: 20px;
            }
          }

          &.social__item--vk {
            .item__btn {
              color: #fff;
              background: #4c75a3;
              transition: background $usTransition;

              &:hover {
                background: #005c8d;
              }

              .icon {
                top: -2px;
              }
            }
          }
        }
      }

      .form__line {
        margin: 15px 0;
        overflow: hidden;

        .line__span {
          position: relative;
          padding: 0 5px;

          &:before,
          &:after {
            content: "";
            position: absolute;
            top: 50%;
            width: 300px;
            height: 1px;
            background: rgba(0, 0, 0, .25);
          }

          &:before {
            right: 100%;
          }

          &:after {
            left: 100%;
          }
        }
      }

      .form__input {
        margin-bottom: 10px;

        label.error {
          display: block;
          margin-top: 5px;
          font-size: 12px;
        }

        .input__item {
          width: 100%;
          box-sizing: border-box;
          padding: 0 10px;
          height: 40px;
          outline: none;
          border: 1px solid transparent;
          border-radius: 2px;

          &:focus {
            border: 1px solid rgba(0, 0, 0, .25);
          }

          &.error {
            border-color: red;
          }
        }
      }

      .form__submit {
        margin-top: 15px;

        .submit__item {
          width: 100%;
        }
      }

      .form__disc {
        margin-top: 15px;
        font-size: 13px;
        color: rgba(0, 0, 0, .8);
      }

      .form__response {
        display: none;
        font-size: 14px;
        margin-top: 15px;
      }
    }

    &.overlay__popup--login {
      max-width: 320px;
      padding: 20px;
      background: rgba(255, 255, 255, .8);
      text-align: center;
    }
  }
}