.boats {
  .boats__map {
    height: 300px;
  }

  .boats__list {
    margin: 0 -10px;
    padding: 0;
    list-style: none;

    .list__item {
      float: left;
      width: 33.333%;
      box-sizing: border-box;
      padding: 0 10px 20px;

      .item__link {
        display: block;
        text-decoration: none;
        color: inherit;
        box-shadow: 1px 1px 5px rgba(0, 0, 0, .3);
        border-radius: 3px;
        transition: color $usTransition;

        &:hover {
          color: #01579B;

          .item__price {
            transform: translateX(0);
            background: rgba(0, 0, 0, 1);
          }
        }
      }

      .item__image {
        position: relative;
        height: 200px;
        overflow: hidden;
        background: {
          size: cover;
          position: center;
        }
      }

      .item__price {
        position: absolute;
        z-index: 2;
        top: 20px;
        left: 0;
        color: #fff;
        background: rgba(0, 0, 0, .2);
        padding: 5px 15px 5px 20px;
        transform: translateX(-5px);
        transition: all $usTransition;
      }

      .item__info {
        padding: 10px;

        .info__line {
          padding-bottom: 5px;

          &:last-child {
            padding-bottom: 0;
          }

          &.info__line--desc {
            font-size: 12px;
          }

          .desc__location {
            .location__text {
              margin-left: 3px;
            }
          }

          .desc__item {
            margin-left: 15px;

            .item__text {
              margin-left: 1px;
            }
          }
        }
      }
    }
  }
}
